@charset "UTF-8";

@import "settings/settings";

.is-fullwidth {
 max-width: 100vw;
 width: 100%;
}

.is-mirrored {
 transform: scaleX(-1);
}

.is-shake-error-animated {
 border-color: $pre !important;
 border-width: 2px !important;
 animation: shake-error-animation 0.5s ease-in;
}

.is-success {
 border-color: $code !important;
 border-width: 2px !important;
}

.media {
 margin: 2rem 0;
}

.topic-section {
 margin-bottom: 2rem;
 margin-top: 2rem;
}

.is-reverse-row {
 display: flex;
 flex-direction: row-reverse;

 .button {
 margin: 0.5rem 0.15rem 1rem;
 }
}

@keyframes shake-error-animation {
 0% {
 transform: translate(1px, 1px) rotate(0deg);
 }

 10% {
 transform: translate(-1px, -2px) rotate(-1deg);
 }

 20% {
 transform: translate(-3px, 0px) rotate(1deg);
 }

 30% {
 transform: translate(3px, 2px) rotate(0deg);
 }

 40% {
 transform: translate(1px, -1px) rotate(1deg);
 }

 50% {
 transform: translate(-1px, 2px) rotate(-1deg);
 }

 60% {
 transform: translate(-3px, 1px) rotate(0deg);
 }

 70% {
 transform: translate(3px, 1px) rotate(-1deg);
 }

 80% {
 transform: translate(-1px, -1px) rotate(1deg);
 }

 90% {
 transform: translate(1px, 2px) rotate(0deg);
 }

 100% {
 transform: translate(1px, -2px) rotate(-1deg);
 }
}


.border-1-dark {
 border: 1rem $primary solid !important;
}

.border-1-light {
 border: 1rem $secondary solid !important;
}

.highlighter-rouge > .highlight {
 padding-bottom: 0.5em;

 pre,
 div.highlight {
 padding: 0 !important;
 background-color: none !important;
 color: none !important;
 }
}

p > code.highlighter-rouge {
 background: $quinary;
 color: $tertiary;
 padding: .25em !important;
 font-size: .8em;
 line-height: 1rem;
}
